import React from "react";
import '@trendmicro/react-sidenav/dist/react-sidenav.css';

import "./App.css"
import "antd/dist/antd.css";
import { Layout, Menu, Icon, Affix, Drawer, Button } from 'antd';
import ListNewsCard from './components/ListNewsCard';
import { getAllPayItems } from './common/APIUtils';
import { bold } from "ansi-colors";

const { Header, Content } = Layout;

class App extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      current: 'tonghop',
      data: null,
      loading: true,
      visible: false,
      screenWidth: null
    }
    this.updateWindowDimensions = this.updateWindowDimensions.bind(this);
  }



  componentWillUnmount() {
    window.removeEventListener("resize", this.updateWindowDimensions)
  }

  updateWindowDimensions() {
    this.setState({ screenWidth: window.innerWidth });
  }

  componentDidMount() {
    window.addEventListener("resize", this.updateWindowDimensions());

    getAllPayItems("").then((response) => {
      this.setState({ data: response.data, loading: false })
    })

  }


  showDrawer = () => {
    this.setState({
      visible: true,
    });
  };

  onClose = () => {
    this.setState({
      visible: false,
    });
  };

  handleClick = (e) => {
    console.log('click ', e);
    this.setState({ loading: true });

    if (e.key === "tonghop") {
      getAllPayItems("").then((response) => {
        this.setState({ data: response.data, current: e.key, loading: false });
      })
    } else {
      getAllPayItems(e.key).then((response) => {
        this.setState({ data: response.data, current: e.key, loading: false });
      })
    }

  }

  buildMenu = (type) => {
    return <div style={{ lineHeight: '64px', margin: 'auto'}}
    >  <Menu
      onClick={this.handleClick}
      selectedKeys={[this.state.current]}
      mode={type}
    >
        <Menu.Item key="tonghop">
          <span style={{ fontWeight: 'bold' }}>Tổng hợp</span>
        </Menu.Item>
        <Menu.Item key="ne">
          <span style={{ fontWeight: 'bold' }}>Thời sự</span>
        </Menu.Item>
        <Menu.Item key="sp">
          <span style={{ fontWeight: 'bold' }}>Thể thao</span>
        </Menu.Item>
        <Menu.Item key="en">
          <span style={{ fontWeight: 'bold' }}>Giải trí</span>
        </Menu.Item>
        <Menu.Item key="ar">
          <span style={{ fontWeight: 'bold' }}>Nghệ thuật</span>
        </Menu.Item>
        <Menu.Item key="fo">
          <span style={{ fontWeight: 'bold' }}>Ẩm thực</span>
        </Menu.Item>
        <Menu.Item key="he">
          <span style={{ fontWeight: 'bold' }}>Sức khỏe</span>
        </Menu.Item>
        <Menu.Item key="bu">
          <span style={{ fontWeight: 'bold' }} disabled>Kinh doanh</span>
        </Menu.Item>
        <Menu.Item key="te">
          <span style={{ fontWeight: 'bold' }}>Công nghệ</span>
        </Menu.Item>
        <Menu.Item key="mo">
          <span style={{ fontWeight: 'bold' }}>Ôtô</span>
        </Menu.Item>
        <Menu.Item key="tr">
          <span style={{ fontWeight: 'bold' }}>Du lịch</span>
        </Menu.Item>
        <Menu.Item key="li">
          <span style={{ fontWeight: 'bold' }}>Đời sống</span>
        </Menu.Item>
      </Menu></div>
  }

  render() {
    console.log(this.state.screenWidth);
    const isMobile = this.state.screenWidth && this.state.screenWidth < 800;
    return (
      <Layout>
        <Header style={{ background: 'white' }}>
          <div className="logo"><img src={'http://2.bp.blogspot.com/--a1hQmVeYLQ/WcABKIqndiI/AAAAAAAASOk/E-fWVFeh2xomut57gjYDD9vlI7eUjzklwCK4BGAYYCw/s1600/Untitled.png'} /></div>
        </Header>
        <Content style={{ padding: '0 10px', background: 'white' }}>
          {<Affix offsetTop={0}>{this.buildMenu("horizontal")}</Affix>}
          <div style={{ background: '#fff', padding: 24, minHeight: 280 }}>
            {this.state.loading ? <div style={{ textAlign: 'center' }}><Icon type="sync" spin style={{ margin: 'auto' }} /></div> : <ListNewsCard isMobile={isMobile} data={this.state.data} />}
          </div>
        </Content>
      </Layout>
    );
  }
}


export default App;
